<template>
  <div id="app">
    <Header />
    <AddTodo v-on:add-todo="addTodo" />
    <Todos v-bind:todos="todos" v-on:del-todo="deleteTodo"/>
    <p class="copyright">Developed by Saksham Raghuvanshi</p>
  </div>
</template>

<script>
import Header from "./components/layout/Header"
import Todos from "./components/Todos";
import AddTodo from "./components/AddTodo";

export default {
  name: 'App',
  components: {
    Header,
    Todos,
    AddTodo
  },
  data() {
    return {
      todos: [
        {
          id:1,
          title:"Prepare speech",
          completed: false
        },
         {
          id:2,
          title:"Hackathon",
          completed: false
        },
         {
          id:3,
          title:"Attend meetup",
          completed: false
        }
      ]
    }
  },
  methods: {
    deleteTodo(id) {
      this.todos = this.todos.filter(todo => todo.id !== id);
    },
    addTodo(newTodo) {
      this.todos =  [...this.todos, newTodo];
    }
  }
}
</script>

